import React from 'react';
import Grid from '@mui/material/Grid';
import { Link } from 'react-router-dom';
import appsConfig from '../config/appsConfig';  // appsConfig
import ActionAreaCard from '../components/ActionAreaCard';  // ActionAreaCard

const Home: React.FC = () => {
    return (
        <Grid container spacing={2} sx={{ padding: 2 }}>
            {appsConfig.map((app) => (
                <Grid item key={app.path} xs={12} sm={6} md={4}>
                    <Link to={app.path} style={{ textDecoration: 'none' }}>
                        <ActionAreaCard
                            title={app.name}  // 
                            description={app.description}  // 
                            image={app.image}  // 
                        />
                    </Link>
                </Grid>
            ))}
        </Grid>
    );
};

export default Home;
